    v-tooltip.top="'text color'"
    is-flat
    icon="format_color_text"
  >
    <q-menu
      anchor="bottom middle"
      self="top middle"
      :offset="[0, 8]"
      auto-close
      transition-show="scale"
      transition-hide="scale"
    >
      <div id="colors">
        <div
          v-for="color in colors"
          :key="color.id"
          class="color"
          :style="{ backgroundColor: color.value }"
