          value: "",
        },
      ],
      activeColor: "",
    };
  },

  methods: {
    handleInput(color) {
      this.activeColor = color.value;

      if (color.label === "none") {
        this.$emit("unset");
        return;
      }

      this.$emit("set", color.value);
    },
  },
};
