<template>
  <BaseActionButton v-tooltip.top="'add field'" is-flat icon="eva-plus">
    <q-menu
      anchor="bottom middle"
      self="top middle"
      transition-show="scale"
      transition-hide="scale"
    >
      <div class="row border">
        <div class="title-3 q-pa-xs col-auto">
          <input
            ref="searchQuery"
            v-model="searchColumns"
            type="text"
            class="q-pa-md"
            placeholder="Search"
            autofocus
            @keyup="searchColumnList(searchColumns)"
          />
        </div>
      </div>
      <BaseScrollbar height="200px">
        <ListItem
          v-for="field in fields"
          :key="field.id"
          :label="field.label"
          :color="field.color"
          class="text-capitalize"
          :class="field.display"
          @click="$emit('click', field)"
        />
      </BaseScrollbar>
    </q-menu>
  </BaseActionButton>
</template>

<script>
import ListItem from "@/components/common/ListItem.vue";

export default {
  name: "AddField",

  components: { ListItem },

  props: {
    formColumns: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      fields: [],
      searchColumns: "",
    };
  },

  watch: {
    formColumns: {
      immediate: true,
      handler() {
        if (this.formColumns.length) {
          this.formColumns.forEach((column) => {
            this.fields.push({
              id: this.$nano.id(),
              label: column.label,
              value: column.hasDefault ? column.value : `#F#${column.label}#F#`,
              display: "",
              color: column.hasDefault ? column.color : "#00bcd4",
            });
          });
        }
      },
    },
  },
  methods: {
    searchColumnList(value) {
      this.fields.forEach((field) => {
        field.display = "";
        if (value) {
          if (!field.label.toLowerCase().includes(value.toLowerCase())) {
            field.display = "hide";
          }
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.q-menu .border {
  border: 1px solid var(--divider-color) !important;
}
#list-item.hide {
  display: none !important;
}
</style>
